(() => {
  const header = document.querySelector(`.header`);
  const main = document.querySelector(`.main`);
  const footer = document.querySelector(`.footer`);
  const popup = document.querySelector(`.popup`);
  const popupClose = popup.querySelector(`.popup__close`);
  const popupBefore = popup.querySelector(`.popup__before`);
  const popupAfter = popup.querySelector(`.popup__after`);
  const popupAfterClose = popup.querySelector(`.popup__after-close`);
  const popupInputs = document.querySelectorAll(`.popup__input`);

  popup.inert = true;

  function onPopupEscPress(e) {
    if (e.key === `Escape`) {
      e.preventDefault();
      closePopup();
    }
  }

  function onPopupClose(e) {
    if (e.target === popup) {
      closePopup();
    }
  }

  function openPopup() {
    header.inert = true;
    main.inert = true;
    footer.inert = true;

    popup.inert = false;
    popup.style.opacity = `1`;
    popup.classList.remove(`visually-hidden`);
    popupBefore.inert = false;
    popupBefore.classList.remove(`visually-hidden`);
    popupAfter.inert = true;
    popupAfter.classList.add(`visually-hidden`);
    document.addEventListener(`keydown`, onPopupEscPress);
    document.addEventListener(`click`, onPopupClose);
  }

  function closePopup() {
    header.inert = false;
    main.inert = false;
    footer.inert = false;

    popup.inert = true;
    popup.style.opacity = `0`;
    popup.classList.add(`visually-hidden`);
    popupBefore.inert = true;
    popupBefore.classList.remove(`visualy-hidden`);
    popupAfter.inert = true;
    popupAfter.classList.add(`visually-hidden`);
    document.removeEventListener(`keydown`, onPopupEscPress);
    document.removeEventListener(`click`, onPopupClose);

    for (let input of popupInputs) {
      input.value = ``;
    }
  }

  document.addEventListener(`click`, (e) => {
    if (e.target.classList.contains(`popup-open`)) {
      e.preventDefault();
      openPopup();
    }
  });

  popupClose.addEventListener(`click`, (e) => {
    e.preventDefault();
    closePopup();
  });

  popupAfterClose.addEventListener(`click`, (e) => {
    e.preventDefault();
    closePopup();
  });

  window.popupFunc = {
    esc: onPopupEscPress,
    close: onPopupClose,
  };
})();
